body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.gallery img {
  width: 200px;
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: transform .2s;
}

.gallery img:hover {
  transform: scale(1.05);
}

.modal {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .8);
  padding-top: 60px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  width: 80%;
  max-width: 882px;
  height: 520px;
  text-align: center;
  background-color: #fefefe;
  border: 1px solid #888;
  margin: 5% auto;
  padding: 20px;
  display: flex;
  position: relative;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  position: absolute;
  top: 10px;
  right: 20px;
}

.close:hover, .close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.image-modal {
  width: 100%;
  height: 472px;
  object-fit: cover;
  border-radius: 5px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.modal-image-div {
  max-width: 350px;
  max-height: 472px;
  margin-right: 20px;
}

.modal-block {
  flex-direction: column;
  display: flex;
}

